body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

#root table{
  border-collapse: collapse;
  margin: 50px auto;
  background: #fff;
}

#root table td{
  border: 5px solid #fff;
  width: 100px;
  height: 100px;
  border-radius: 16px;
  background: #303030;
  color: #fff;
  font-size: 36px;
  text-align: center;
}
#root table td.game-block-value-2{
  background: #2196f3;
}
#root table td.game-block-value-4{
  background: #4caf50;
}
#root table td.game-block-value-8{
  background: #9c27b0;
}
#root table td.game-block-value-16{
  background: #ff5252;
}
#root table td.game-block-value-32{
  background: #e91e63;
}
#root table td.game-block-value-64{
  background: #cddc39;
}
#root table td.game-block-value-128{
  background: #3f51b5;
}
#root table td.game-block-value-256{
  background: #ff9800;
}
#root table td.game-block-value-512{
  background: #673ab7;
}
#root table td.game-block-value-1024{
  background: #009688;
}
#root table td.game-block-value-2048{
  background: #03a9f4;
}
h1{
  text-align: center;
  font-size: 3.5em;
  color: #303030;
}
.header p{
  background: transparent;
  border-radius: 0;
  border-width: 0;
  font-weight: 400;
  margin: 0;
  padding: 1vw 2vw;
  position: relative;
  z-index: 0;
  text-align: center;
  width: 5em;
}
.header .header-score{
  display: -ms-flexbox;
  display: flex;
  width: 400px;
  margin: 20px auto;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  color: #fff;
  font-size: 12px;
}
.header p.header-score-span:before {
  content: "";
  height: 100%;
  width: 100%;
  left: 0;
  position: absolute;
  top: 0;
  -webkit-transform: skewX(25deg);
  -ms-transform: skewX(25deg);
      transform: skewX(25deg);
  z-index: -1;
  background: #2196f3;
}
.header p.header-score-span:after{
  background: hsla(0,0%,98%,.3);
  opacity: 0;
  width: 0;
}
#root .reset{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}
button{
  background: #2196f3;
    color: #fff;
    width: 100px;
    height: 36px;
    border: none;
    -webkit-box-shadow: 1px 1px 3px rgb(89, 0, 255);
            box-shadow: 1px 1px 3px rgb(89, 0, 255);
    border-radius: 4px;
    font-size: 1.1rem;
}


